<template>
    <div class="shipping-address">
        <div class="address-title clearfix">
            <h4></h4>
            <button class="add-address" @click="addNewAddress">+</button>
        </div>
        <table width="100%" v-for="(item, index) in addressList">
            <colgroup>
                <col width="108px">
                <col width="390px">
                <col width="207px">
                <col width="144px">
                <col width="141px">
            </colgroup>
            <tbody>
            <tr>
                <td>{{ item.name }}</td>
                <td>{{ item.address }}</td>
                <td>{{ item.phone }}</td>
                <td class="td-edit">
                    <span data-toggle="modal" @click="editModal"></span><span class="line">|</span>
                    <span @click="deleteAddress(item)"></span></td>
                <td class="td-address" v-if="item.isdefault == 1"></td>
                <td class="address" @click="settingAddress(item)" v-if="item.isdefault == 0"></td>
            </tr>
            </tbody>
        </table>
        <modal ref="modal">
            <div slot="title">
                <h4 class="modal-title" v-text="modalTitle"></h4>
            </div>
            <div slot="body">
                <form class="signup-form">
                    <div class="signup-form-group clearfix">
                        <label class="form-title"></label>
                        <input type="text" class="signup-form-control" name="username" placeholder="">
                    </div>
                    <div class="signup-form-group clearfix">
                        <label class="form-title"></label>
                        <input type="text" class="signup-form-control" name="telphone" placeholder="">
                    </div>
                    <div class="signup-form-group clearfix">
                        <label class="form-title"></label>
                        <div class="signup-form-control select-province">
                            <select>
                                <option value="0"></option>
                            </select>
                            <select>
                                <option value="0"></option>
                            </select>
                            <select>
                                <option value="0"></option>
                            </select>
                        </div>
                    </div>
                    <div class="signup-form-group clearfix group-address">
                        <label class="form-title"></label>
                        <textarea type="text" class="signup-form-control details-address" name="address"
                                  placeholder="50"></textarea>
                    </div>
                    <div class="signup-form-group default-address clearfix">
                        <label class="form-title"></label>
                        <div class="check-box">
                            <span><input type="checkbox" class="input_check" id="check46"><label for="check46"> </label></span>
                        </div>
                        <span class="default"></span>
                    </div>
                </form>
            </div>
            <button type="button" class="order-btn" slot="save_address"></button>
        </modal>
    </div>
</template>

<script>
    import Modal from './Modal';

    export default {
        components: { Modal },
        data() {
            return {
                addressList: [
                    {
                        address: '     36 xx',
                        isdefault: 1,
                        name: '',
                        phone: 13000000000,
                    },
                    {
                        address: '     36 xx',
                        isdefault: 1,
                        name: '',
                        phone: 13000000000,
                    },
                    {
                        address: '     36 xx',
                        isdefault: 1,
                        name: '',
                        phone: 13000000000,
                    },
                ],
                modalTitle: '',
                status: 0,
            };
        },
        methods: {
            addNewAddress() {
                this.$refs.modal.open();
                this.modalTitle = '';
            },
            deleteAddress(item) {
                const index = this.addressList.indexOf(item);
                if (index !== -1) {
                    this.addressList.splice(index, 1);
                }
            },
            editModal() {
                this.$refs.modal.open();
                this.modalTitle = '';
            },
            settingAddress(item) {
                this.addressList.forEach(index => {
                    index.isdefault = 0;
                });
                item.isdefault = 1;
            },
        },
    };
</script>


