<template>
    <div class="header-bar">
        <div class="container">
            <div class="header-welcome ">
                !
                <router-link :to="{ name: 'signin' }" class="login"></router-link>
                <span>|</span>
                <router-link :to="{ name: 'signup' }" class="register"></router-link>
            </div>
            <div class="header-bar-user ">
                <ul class="header-bar-nav">
                    <li v-for="item in navigation" :class="{ 'active': $route.path === item.to }">
                        <router-link :to="item.to">{{ item.text }}</router-link>
                        <span>|</span>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                navigation: [
                    {
                        to: { name: 'all-order' },
                        text: '',
                    },
                    {
                        to: { name: 'personnal-center' },
                        text: '',
                    },
                    {
                        to: { name: 'customer-serve' },
                        text: '',
                    },
                    {
                        to: { name: 'more' },
                        text: '',
                    },
                    {
                        to: { name: 'businessmen' },
                        text: '',
                    },
                ],
            };
        },
    };
</script>



